import { CDocumentHints } from 'client/src/services/wasm-worker/documentHints';

declare var Module: IWASMModule;

import { allocateMemory, freeMemory } from 'client/src/services/wasm-worker/memoryHelper';
import {
  HEAP_TYPE_ENUM,
  TErrorCode,
  TErrorCodeDescription,
  TPixelFormat,
  WasmCropTypes,
  WasmConfigTypes,
  WasmEdgesDetectionResultFlags,
} from '../wasm/CONSTANTS';
import { IFrameReport, IPointer, IWasmImageResult, IWASMModule } from 'client/src/services/wasm-worker/wasmTypes';
import { IPoint, SuitabilityForOcrVerdict, TQualityAssessmentResult } from 'client/src/types';
import { arrayToPoints, pointsToArray } from 'client/src/services/wasm/utils';
import { TJpegResult } from 'client/src/types/declaration';
import CanvasHelper from 'client/src/services/wasm/canvasHelper';

/**
 * Class for directly working with WASM module
 * call methods, handle memory, and parse result to js objects
 */
class Wasm {
  /**
   * for performance, mutable,
   * result of WASM frame processing
   */
  private frameReport: IPointer;
  /**
   * status of WASM last operation
   */
  private status: IPointer;

  /**
   * Call _WasmDetectEdges method from WASM two times
   * first with %selected% detector, then with Fast detector (if %selected% found nothing)
   * helper method for DetectEdges&Crop
   * @param cImgPointer
   * @param imagePointer
   * @param pointsPointer
   * @param edgesDetectionResultPointer
   * @param {WasmCropTypes} cropType
   */
  private detectEdgesWithDetectors = (
    cImgPointer: IPointer,
    imagePointer: IPointer,
    pointsPointer: IPointer,
    edgesDetectionResultPointer: IPointer,
    cropType = WasmCropTypes.Quality // by default we looking for edges with Quality detector
  ) => {
    const errorCode = Module._WasmDetectEdges(
      cImgPointer.ptr,
      imagePointer.ptr,
      TPixelFormat.PF_RGBA8888,
      pointsPointer.ptr,
      edgesDetectionResultPointer.ptr
    );

    // edges found or error or crop was selected
    if (
      edgesDetectionResultPointer.getData()[0] !== WasmEdgesDetectionResultFlags.NotFound ||
      errorCode ||
      cropType !== undefined
    ) {
      return errorCode;
    }

    return Module._WasmDetectEdges(
      cImgPointer.ptr,
      imagePointer.ptr,
      TPixelFormat.PF_RGBA8888,
      pointsPointer.ptr,
      edgesDetectionResultPointer.ptr
    );
  };

  public detectEdges = (bmpData: Uint8ClampedArray, imagewidth: number, imageheight: number, cropType?: number) => {
    const cImgPointer = allocateMemory(CanvasHelper.getcImageStruct(imagewidth, imageheight));
    const imagePointer = allocateMemory(bmpData, HEAP_TYPE_ENUM.HEAP8);

    // corner points  data
    const pointsPointer = allocateMemory(new Int32Array(8));
    const edgesDetectionResultPointer = allocateMemory(new Int32Array(1));

    const errorCode = this.detectEdgesWithDetectors(
      cImgPointer,
      imagePointer,
      pointsPointer,
      edgesDetectionResultPointer,
      cropType
    );

    if (
      errorCode === TErrorCode.NoError &&
      edgesDetectionResultPointer.getData()[0] === WasmEdgesDetectionResultFlags.NotFound
    ) {
      // no edges detected, clear wasm memory pollution
      pointsPointer.setData(new Int32Array([0, 0, 0, 0, 0, 0, 0, 0]));
    }

    const foundEdges = arrayToPoints(pointsPointer.getData());

    freeMemory(cImgPointer);
    freeMemory(imagePointer);
    freeMemory(pointsPointer);
    freeMemory(edgesDetectionResultPointer);

    if (errorCode) {
      throw new Error('Error detectEdgesAndCrop: ' + TErrorCode[errorCode]);
    }

    return foundEdges;
  };

  /**
   * Determine crop boundaries and crop image, result in jpeg.
   * Usage: get original image in jpeg and crop image from gallery/device camera
   * @param {Uint8ClampedArray} bmpData - image data, bmp
   * @param {number} imagewidth - image width
   * @param {number} imageheight - image height
   * @param {number} imageQuality - quality of resulted image, 1-100%, 80% by default
   * @param {number} cropType - crop type of boundaries detection (quality, fast)
   */
  public detectEdgesAndCrop = (
    bmpData: Uint8ClampedArray,
    imagewidth: number,
    imageheight: number,
    imageQuality: number,
    cropType?: number
  ) => {
    let result: TJpegResult = null;

    const cImgPointer = allocateMemory(CanvasHelper.getcImageStruct(imagewidth, imageheight));
    const imagePointer = allocateMemory(bmpData, HEAP_TYPE_ENUM.HEAP8);
    // corner points  data

    const pointsPointer = allocateMemory(new Int32Array(8));
    const edgesDetectionResultPointer = allocateMemory(new Int32Array(1));

    let errorCode = this.detectEdgesWithDetectors(
      cImgPointer,
      imagePointer,
      pointsPointer,
      edgesDetectionResultPointer,
      cropType
    );

    if (errorCode === TErrorCode.NoError) {
      // no edges detected
      if (edgesDetectionResultPointer.getData()[0] === WasmEdgesDetectionResultFlags.NotFound) {
        pointsPointer.setData(new Int32Array([0, 0, 0, 0, 0, 0, 0, 0]));
      }

      const resultedPointerSize = allocateMemory(new Int32Array(1));

      // override(for performance) bmp imagePointer with jpeg result
      errorCode = Module._WasmGetCroppedJpegImage(
        cImgPointer.ptr,
        imagePointer.ptr,
        TPixelFormat.PF_RGBA8888,
        pointsPointer.ptr,
        1,
        resultedPointerSize.ptr,
        imagePointer.ptr,
        imagePointer.size,
        imageQuality
      );

      if (errorCode === TErrorCode.NoError) {
        const croppedImageData = imagePointer.getData(resultedPointerSize.getData()[0]);

        result = {
          data: croppedImageData,
          edges: arrayToPoints(pointsPointer.getData()),
        };
      }

      freeMemory(resultedPointerSize);
    }

    freeMemory(cImgPointer);
    freeMemory(imagePointer);
    freeMemory(pointsPointer);
    freeMemory(edgesDetectionResultPointer);

    if (errorCode) {
      throw new Error('Error detectEdgesAndCrop: ' + TErrorCode[errorCode]);
    }

    return result;
  };

  /**
   * Check if image ready for optical recognition
   * @param {Uint8ClampedArray} jpegData - image data jpeg
   * @param {IPoint[]} vertices - crop vertices
   * @returns { TQualityAssessmentResult } - wasm estimator result
   */
  public checkQualityAssessmentForOCR(jpegData: Uint8ClampedArray, vertices: IPoint[]): TQualityAssessmentResult {
    let result: TQualityAssessmentResult = null;

    const bufferPointer = allocateMemory(jpegData, HEAP_TYPE_ENUM.HEAP8);
    const pointsPointer = allocateMemory(pointsToArray(vertices));
    const verdictPointer = allocateMemory(new Uint32Array(1));
    const confidencePointer = allocateMemory(new Float64Array(1), HEAP_TYPE_ENUM.HEAPF64);

    const errorCode = Module._WasmIsSuitableForOcrJpeg(
      bufferPointer.ptr,
      jpegData.length,
      pointsPointer.ptr,
      1,
      verdictPointer.ptr,
      confidencePointer.ptr
    );

    if (!errorCode) {
      const verdict: SuitabilityForOcrVerdict = verdictPointer.getData()[0];
      const confidence: number = confidencePointer.getData()[0];

      result = {
        verdict,
        confidence,
      };
    }

    freeMemory(bufferPointer);
    freeMemory(pointsPointer);
    freeMemory(verdictPointer);
    freeMemory(confidencePointer);

    if (errorCode) {
      throw new Error('Error checkQualityAssessmentForOCR: ' + TErrorCode[errorCode]);
    }

    return result;
  }

  /**
   * recrop image by new boundaries
   * @param {Uint8ClampedArray} jpegData - image data
   * @param {IPoint[]} documentBoundary - crop coords
   * @param {number} imageQuality - jpeg quality, 0-100
   */
  public recropImage(
    jpegData: Uint8ClampedArray,
    documentBoundary: Int32Array,
    imageQuality: number
  ): Uint8ClampedArray {
    // source image data
    const sourcePointer = allocateMemory(jpegData, HEAP_TYPE_ENUM.HEAP8);
    // corner points  data
    const pointsPointer = allocateMemory(documentBoundary);
    // size of resulted image would be placed here
    const resultedBufferSizePointer = allocateMemory(new Uint32Array(1));
    // in case if resulted jpeg would be larger, than source we multiply allocated buffer by 2
    // for small jpeg jpegData.length is much more less relative to size of file because of size of jpeg header.
    // Set minimal buffer size to 1 MB.
    // Will be fix in US 54815 https://tfs/HQ/MobileSDK/_workitems/edit/54815
    const targetBufferSize = Math.max(1e6, jpegData.length * 2);
    // resulted jpg will be placed here
    const targetBufferPointer = allocateMemory(new Uint8ClampedArray(targetBufferSize), HEAP_TYPE_ENUM.HEAP8);

    const errorCode = Module._WasmRecropJpegImage(
      sourcePointer.ptr,
      sourcePointer.size,
      pointsPointer.ptr,
      1,
      resultedBufferSizePointer.ptr, // TODO remove this from API
      targetBufferPointer.ptr,
      targetBufferSize,
      imageQuality
    );

    const size = resultedBufferSizePointer.getData()[0];
    const result = targetBufferPointer.getData(size);

    freeMemory(sourcePointer);
    freeMemory(pointsPointer);
    freeMemory(resultedBufferSizePointer);
    freeMemory(targetBufferPointer);

    if (errorCode) {
      throw new Error('Error recrop Image: ' + TErrorCode[errorCode]);
    }

    return result;
  }

  /**
   * check license for validness
   * @param {ArrayBuffer} licenseFileBuffer
   */
  public checkLicense = (licenseFileBuffer: ArrayBuffer) => {
    const data = allocateMemory(new Uint8Array(licenseFileBuffer), HEAP_TYPE_ENUM.HEAP8);
    const errorCode = Module._WasmSetLicense(data.ptr, data.size);

    freeMemory(data);

    if (errorCode) {
      throw new Error(TErrorCodeDescription[errorCode]);
    }
  };

  /**
   * Run new session, looking for stable image
   * duration of session(from last bestFrame): numberOfFrames > minFrameCount && time > stabilizationTime
   * @param {Number} minFramesCount - min number of stable frames to wait before decision than best frame found.
   * must be >= 2
   * @param stabilizationTime - min time from bestFrame, before we are ready to say, that one can't find better frame
   * @param frameBoundaryPadding - corners must be within % margin from screen edges(50% = whole screen)
   * @param enableSharpnessFilter - enable sharpness filter
   * @param cropType - which crop use for detecting boundaries
   */
  public startImageDetection(
    minFramesCount: number,
    stabilizationTime: number,
    frameBoundaryPadding: number,
    enableSharpnessFilter: boolean,
    cropType = WasmCropTypes.Fast
  ): void {
    this.setConfig(WasmConfigTypes.SetOffsetFromBoundaries, frameBoundaryPadding);

    enableSharpnessFilter
      ? this.setConfig(WasmConfigTypes.SetImageQualityMinValue, 10)
      : this.setConfig(WasmConfigTypes.SetImageQualityMinValue, -1);

    // Third parameter - inertial detection of edges. Value - number of skipped frames ( detected based on motion detection )
    const errorCode = Module._WasmStartImageDetection(minFramesCount, stabilizationTime, 0);

    if (errorCode) {
      throw new Error('Error startImageDetection: ' + TErrorCode[errorCode]);
    }

    // allocate memory for frame results
    if (!this.frameReport) {
      this.frameReport = allocateMemory(new Int32Array(12));
    }
    if (!this.status) {
      this.status = allocateMemory(new Int32Array(1));
    }
  }

  /**
   * Stop image detection session
   */
  public stopImageDetection(): void {
    const errorCode = Module._WasmStopImageDetection();

    freeMemory(this.frameReport);
    freeMemory(this.status);

    if (errorCode) {
      throw new Error('Error stopImageDetection: ' + TErrorCode[errorCode]);
    }

    this.frameReport = null;
    this.status = null;
  }

  /**
   * add new bitmap image to current capture session
   * @returns {Object} IWasmImageResult
   */
  public addImage(imageDataPointerPtr: number, cImageStructPointerPtr: number): IWasmImageResult {
    const errorCode = Module._WasmAddImage(
      cImageStructPointerPtr,
      imageDataPointerPtr,
      TPixelFormat.PF_RGBA8888,
      this.frameReport.ptr,
      this.status.ptr
    );

    if (errorCode) {
      throw new Error('Error stopImageDetection: ' + TErrorCode[errorCode]);
    }

    return {
      ...this._getFrameReport(),
      status: this.status.getData()[0],
    };
  }

  /**
   * generate result object from wasm pointer to memory
   * @private
   */
  private _getFrameReport(): IFrameReport {
    const frameReport: Int32Array = this.frameReport.getData();

    return {
      quality: frameReport[0],
      imageQualityFlags: frameReport[1],
      edgesDetectionResultFlags: frameReport[2],
      vertices: frameReport.slice(3, 11),
      isNewBest: !!frameReport[11],
    };
  }

  /**
   *
   * @param param, currently only 0 = selected crop
   * @param value, for 0(selected crop ) = 0,1 - quality, fast
   */
  private setConfig(param: number, value: number) {
    const valuePtr = allocateMemory(new Int32Array([value]));

    Module._WasmConfig(param, valuePtr.ptr);

    freeMemory(valuePtr);
  }

  public setDocumentConfig(documentHints: CDocumentHints) {
    const DocumentHints = allocateMemory(
      new Float64Array([
        documentHints.aspectRatioMin,
        documentHints.aspectRatioMax,
        documentHints.focalLength,
        documentHints.documentSize.width,
        documentHints.documentSize.height,
      ]),
      HEAP_TYPE_ENUM.HEAPF64
    );

    const errorCode = Module._WasmConfig(WasmConfigTypes.SetDocumentHints, DocumentHints.ptr);

    if (errorCode) {
      throw new Error('Error setting wasm config: ' + TErrorCode[errorCode]);
    }

    freeMemory(DocumentHints);
  }
}

export default new Wasm();
