import CanvasHelper from 'client/src/services/wasm/canvasHelper';
import { appSettings } from 'client/src/services/settings';
import { IPointer } from 'client/src/services/wasm-worker/wasmTypes';
import { HEAP_TYPE_ENUM, WasmCropTypes } from 'client/src/services/wasm/CONSTANTS';
import { allocateMemory, freeMemory } from 'client/src/services/wasm-worker/memoryHelper';
import wasm from './wasm';

/**
 * small helper class to work with liveCapture in web-worker mainly for allocating memory and freeing it after
 * process ends
 */
class CaptureProcessWorker {
  // allocated memory to run per frame without reallocation memory each time
  private cImageStructPointer: IPointer;
  private imageDataPointer: IPointer;

  public start(
    width: number,
    height: number,
    frameBoundaryPadding: number,
    enableSharpnessFilter: boolean,
    cropType?: WasmCropTypes
  ) {
    this.cImageStructPointer = allocateMemory(CanvasHelper.getcImageStruct(width, height));

    wasm.startImageDetection(
      appSettings.capture.minFramesCount,
      appSettings.capture.minCaptureTime,
      frameBoundaryPadding,
      enableSharpnessFilter,
      cropType
    );
  }

  public captureNextFrame = (bmpData: Uint8ClampedArray) => {
    if (!this.imageDataPointer) {
      this.imageDataPointer = allocateMemory(bmpData, HEAP_TYPE_ENUM.HEAP8);
    }

    this.imageDataPointer.setData(bmpData);

    return wasm.addImage(this.imageDataPointer.ptr, this.cImageStructPointer.ptr);
  };

  public stop = () => {
    wasm.stopImageDetection();
    freeMemory(this.cImageStructPointer);
    freeMemory(this.imageDataPointer);

    this.cImageStructPointer = null;
    this.imageDataPointer = null;
  };
}

export default new CaptureProcessWorker();
