import { IHeapType, IPointer } from 'client/src/services/wasm-worker/wasmTypes';
import { HEAP_TYPE_ENUM } from 'client/src/services/wasm/CONSTANTS';

/**
 * WASM data types
 */
const HEAP_TYPE = {
  [HEAP_TYPE_ENUM.HEAP8]: {
    bytesPerEl: 1,
    heap: 'HEAPU8',
    array: 'Uint8ClampedArray',
  },
  [HEAP_TYPE_ENUM.HEAP32]: {
    bytesPerEl: 4,
    heap: 'HEAP32',
    array: 'Int32Array',
  },
  [HEAP_TYPE_ENUM.HEAPF32]: {
    bytesPerEl: 4,
    heap: 'HEAPF32',
    array: 'Float32Array',
  },
  [HEAP_TYPE_ENUM.HEAPF64]: {
    bytesPerEl: 8,
    heap: 'HEAPF64',
    array: 'Float64Array',
  },
};

/**
 * Memory wrapper, helps working with memory more convenient
 */
class Pointer implements IPointer {
  public ptr: number;
  public heapType: IHeapType;
  public size: number;

  /**
   * create pointer with predefined size in heapType
   * @param data
   * @param heapType
   */
  constructor(data: ArrayLike<number>, heapType: IHeapType) {
    this.heapType = heapType;
    this.size = data.length;
    this.ptr = Module._malloc(this.size * heapType.bytesPerEl);
    this.setData(data);
  }

  public setData(data: ArrayLike<number>) {
    (Module as any)[this.heapType.heap].set(data, this.ptr / this.heapType.bytesPerEl);
  }

  public getData(newSize: number = 0): any {
    // TODO tsc ArrayLike<number>
    const size = newSize || this.size;

    // HEAP_TYPE
    // @ts-ignore
    const array: Int32Array = new window[this.heapType.array](size); // TODO tsc
    const pos: number = this.ptr / this.heapType.bytesPerEl;

    array.set((Module as any)[this.heapType.heap].subarray(pos, pos + size));
    return array;
  }
}

export const allocateMemory = (data: ArrayLike<number>, heapType = HEAP_TYPE_ENUM.HEAP32) => {
  return new Pointer(data, HEAP_TYPE[heapType]);
};

export const freeMemory = (ptr: Pointer) => {
  Module._free(ptr.ptr);
};
