import { IWASMModule } from 'client/src/services/wasm-worker/wasmTypes';
import { AnyAction } from 'redux';
import registerPromiseWorker from 'promise-worker-transferable/register';
import WASM_ACTION_TYPES from 'client/src/services/wasm-worker/WASM_ACTION_TYPES';
import wasm from './wasm';
import captureProcessWorker from 'client/src/services/wasm-worker/captureProcessWorker';

declare var Module: IWASMModule;

if (typeof window === 'undefined') {
  (self as any).window = self; // webpack hot-reload bug
}

interface IWasmWorkerMessage {
  type: WASM_ACTION_TYPES;
}

const actions: {
  [key in WASM_ACTION_TYPES]?: (
    action: AnyAction,
    withTransferList?: (action: AnyAction, buffer?: Array<ArrayBuffer | SharedArrayBuffer>) => Promise<AnyAction>
  ) => Promise<AnyAction>;
} = {};

/**
 * main entry point in WASM web worker
 */
registerPromiseWorker(async (message: IWasmWorkerMessage | string, withTransferList) => {
  // ping-pong messages
  if (typeof message === 'string') {
    return {
      type: WASM_ACTION_TYPES.OK,
    };
  }

  const actionType = message.type;

  if (actions[actionType]) {
    try {
      return await actions[actionType](message, withTransferList);
    } catch (e) {
      window.console.error('Error performing wasm action: ' + actionType);
      window.console.error(e);

      return Promise.reject({
        error: e.toString(),
        type: WASM_ACTION_TYPES.ERROR,
      });
    }
  } else {
    throw new Error(`Wasm action ${actionType} not supported`);
  }
});

/*
 *  List of all possible actions below
 */

/**
 * load wasm scripts and wait for was initialization
 * @param data
 */
actions[WASM_ACTION_TYPES.LOAD_WASM] = async (data) => {
  importScripts('/' + data.path + '/wasm_engine.js?version=1.8.1_07-04-20,05:16:15');

  await new Promise((resolve) => {
    const checkInit = () => {
      if (Module.calledRun) {
        resolve();
      } else {
        setTimeout(checkInit, 100);
      }
    };

    checkInit();
  });

  return {
    type: WASM_ACTION_TYPES.OK,
  };
};

/**
 * load and check wasm license
 * @param data
 */
actions[WASM_ACTION_TYPES.CHECK_LICENSE] = async (data) => {
  const licenceBuffer = await fetch(data.licenseFilePath).then((response) => response.arrayBuffer());

  wasm.checkLicense(licenceBuffer);

  return {
    type: WASM_ACTION_TYPES.OK,
  };
};

/**
 * load and check wasm license
 * @param data
 */
actions[WASM_ACTION_TYPES.SET_DOCUMENT_CONFIG] = async (data) => {
  wasm.setDocumentConfig(data.documentHints);

  return {
    type: WASM_ACTION_TYPES.OK,
  };
};

/**
 * detect edges on image
 * @param data
 * @param withTransferList
 */
actions[WASM_ACTION_TYPES.DETECT_EDGES] = async (data, withTransferList) => {
  const { bmpData, imageWidth, imageHeight, cropType } = data;
  const edges = wasm.detectEdges(bmpData, imageWidth, imageHeight, cropType);

  return withTransferList({
    result: { edges },
    type: WASM_ACTION_TYPES.OK,
  });
};

/**
 * detect edges and crop image
 * @param data
 * @param withTransferList
 */
actions[WASM_ACTION_TYPES.DETECT_EDGES_AND_CROP] = async (data, withTransferList) => {
  const { bmpData, imageWidth, imageHeight, imageQuality, cropType } = data;
  const result = wasm.detectEdgesAndCrop(bmpData, imageWidth, imageHeight, imageQuality, cropType);

  return withTransferList(
    {
      result,
      type: WASM_ACTION_TYPES.OK,
    },
    [result.data.buffer]
  );
};

/**
 * check if image suitable for optical recognize
 * @param data
 */
actions[WASM_ACTION_TYPES.CHECK_QUALITY_OCR] = async (data) => {
  const { jpegData, documentBoundary } = data;

  return {
    result: wasm.checkQualityAssessmentForOCR(jpegData, documentBoundary),
    type: WASM_ACTION_TYPES.OK,
  };
};

/**
 * recrop given image with new edges
 * @param data
 * @param withTransferList
 */
actions[WASM_ACTION_TYPES.RECROP_IMAGE] = async (data, withTransferList) => {
  const { jpegData, documentBoundary, imageQuality } = data;
  const result = wasm.recropImage(jpegData, documentBoundary, imageQuality);

  return withTransferList(
    {
      result,
      type: WASM_ACTION_TYPES.OK,
    },
    [result.buffer]
  );
};

/**
 * start capture process, allocate memory and start wasm process
 * @param data
 */
actions[WASM_ACTION_TYPES.START_IMAGE_DETECTION] = async (data) => {
  await captureProcessWorker.start(data.width, data.height, data.frameBoundaryPadding, data.enableSharpnessFilter);

  return {
    type: WASM_ACTION_TYPES.OK,
  };
};

/**
 * send new frame to capture process and return result
 * @param data
 */
actions[WASM_ACTION_TYPES.CAPTURE_NEXT_FRAME] = async (data) => {
  const result = await captureProcessWorker.captureNextFrame(data.bmpData);

  return {
    result,
    type: WASM_ACTION_TYPES.OK,
  };
};

/**
 * stop capture process, free allocated memory
 */
actions[WASM_ACTION_TYPES.STOP_IMAGE_DETECTION] = async () => {
  captureProcessWorker.stop();

  return {
    type: WASM_ACTION_TYPES.OK,
  };
};
